
*, *::before, *::after {
	margin: 0;
	padding: 0;
	list-style: none;
	outline: 0;
	background: none;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font: inherit;
	font-family: inherit;
	font-size: 100%;
	font-style: inherit;
	font-weight: inherit;
	text-decoration: none;
	vertical-align: baseline;
	-webkit-font-feature-settings: "palt";
	font-feature-settings: "palt";
}

html {
	font-family: sans-serif;
	font-size: 62.5%;
	-webkit-font-smoothing: antialiased;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	-webkit-tap-highlight-color: transparent;
	position: relative;
	min-height: 100%;
	-ms-touch-action: manipulation;
	touch-action: manipulation;
}

body {
	font-family: open sans,helvetica neue,Helvetica,Arial,sans-serif;
	font-size: 1.4rem;
	line-height: 1.3;
	color: #333;
}

a {
	font-size: 1.6rem;
	
	&:hover {
		color: #f00;
	}
}

h1 {
	font-size: 1.6rem;
	transition: all .3s;

	&:hover {
		color: #f00;
	}
}

img {
	width: auto;
	max-width: 100%;
}

header {
	position: relative;
	margin: 0 0 20px;
	padding: 20px;
}

main {
	width: 100%;
	max-width: 980px;
	margin: 0 auto;
}

.jsonUl {
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-content: flex-start;
	flex-flow: row wrap;
	align-items: stretch;
	
	& > li {
		width: calc((100% - 20px) / 3.0001);
		padding: 10px;
		border-radius: 5px;
		border: 1px solid #ccc;
		
		&:nth-of-type(n + 4) {
			margin-top: 20px;
		}
		&:not(:nth-of-type(3n)) {
			margin-right: auto;
		}

		& > dl > dt {
			width: 100%;
			margin: 0 0 10px;
			font-size: 1.6rem;
		}
		& > dl > dd {
			line-height: 1.5;
			display: flex;
			justify-content: flex-start;
			align-content: flex-start;
			flex-flow: row wrap;
			align-items: flex-start;
			width: 100%;
			$width: 70%;
			p.textBlock {
				width: $width;
				margin-right: auto;
			}

			div.imgBlock {
				width: calc(100% - (10px + #{$width}));
			}
		}
	}
}